import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CFormLabel,
  CCardFooter,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
  CTable,
  CTableHead,
  CTableRow,
  CTableBody,
  CTableDataCell,
  CTableHeaderCell,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import {
  faTimesCircle,
  faCheckCircle,
  faArrowRight,
  faSave,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Formik, Form, Field, FieldArray, ErrorMessage } from "formik";
import * as Yup from "yup";
import Loader from "src/loader/Loader";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";
import { Link } from "react-router-dom";

const create_admin = (props) => {
  let school_id = props.match.params.school_id;
  let [visible, setVisible] = useState(false);
  let [days, setDays] = useState(0);
  let [sDate, setSDate] = useState("");
  let [eDate, setEDate] = useState("");
  let [dataLoaded, setDataLoaded] = useState(false);
  let [showTable, setShowTable] = useState(false);
  let [tableData, setTableData] = useState([]);
  let [classes, setClasses] = useState("");

  // holiday type
  let h_type = [
    { text: "Holiday", value: "holiday" },
    { text: "Exam Calender", value: "exam_calender" },
    { text: "Summer Holiday", value: "summer_holiday" },
  ];
  // open cancle modal box
  let openModalBox = () => {
    setTimeout(() => setVisible(true), 0);
  };

  // close cancle modal box
  let closeModalBox = () => {
    setTimeout(() => setVisible(false), 0);
  };

  // gate today
  let getToday = () => {
    let today = new Date();
    let dd = String(today.getDate()).padStart(2, "0");
    let mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
    let yyyy = today.getFullYear();
    today = yyyy + "-" + mm + "-" + dd;
    return today;
  };

  // gate tomorrow
  let getTomorrow = () => {
    let tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);
    let dd = String(tomorrow.getDate()).padStart(2, "0");
    let mm = String(tomorrow.getMonth() + 1).padStart(2, "0"); //January is 0!
    let yyyy = tomorrow.getFullYear();
    tomorrow = yyyy + "-" + mm + "-" + dd;
    return tomorrow;
  };

  // start date value
  let sDateVal = (e) => {
    setTimeout(() => setSDate(e.target.value), 0);
  };
  // end date value
  let eDateVal = (e) => {
    setTimeout(() => setEDate(e.target.value), 0);
  };
  // aData
  let aDate = (sDate, eDate) => {
    if (sDate != "" && eDate != "") {
      let start = sDate.split("-").reverse();
      start = start[1] + "/" + start[0] + "/" + start[2];
      let end = eDate.split("-").reverse();
      end = end[1] + "/" + end[0] + "/" + end[2];
      var date1 = new Date(start);
      var date2 = new Date(end);
      const diffTime = Math.abs(date2 - date1);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      setDays(diffDays + 1);
    } else {
      setDays(0);
    }
  };
  // formik params
  let initialValues = {
    class: "",
    start_date: sDate,
    end_date: eDate,
  };

  // validation rules of the form fields
  const validationSchema = Yup.object().shape({
    class: Yup.string().required("Class is required"),
    start_date: Yup.string().required("Start date is required"),
    end_date: Yup.string().required("End date is required"),
  });
  // submit form
  const onSubmit = async (values) => {
    await EDU_AXIOS.post(
      `${process.env.REACT_APP_API_PATH}/ttm/generate-time-table`,
      {
        school_id: school_id,
        cl: values.class,
        start_date: values.start_date,
        end_date: values.end_date,
      }
    )
      .then((resp) => {
        setShowTable(true);
        setTableData(resp.data.data);
      })
      .catch((err) => {
        notify("error", err);
      });
  };

  // get constraints detail from server
  useEffect(async () => {
    setDataLoaded(false);
    const res = await getClasses();
    if (res) {
      setDataLoaded(true);
    }
  }, []);

  let showTimeTable = () => {
    let record = [];
    console.log(tableData);
    if (tableData.length > 0) {
      record.push(
        <CRow className="mt-2">
          <CCol md={12}>
            <CTable striped>
              <CTableHead color="dark">
                {tableData.map((r, i) => {
                  if (i === 0) {
                    return (
                      <CTableRow>
                        {r.map((r1) => {
                          return (
                            <CTableDataCell>
                              <span style={{ fontSize: "12px" }}>
                                {r1.period}
                              </span>
                              <br />
                              <span
                                style={{
                                  fontSize: "10px",
                                  fontWeight: "bolder",
                                }}
                              >
                                {`${r1.start} - ${r1.end}`}
                              </span>
                            </CTableDataCell>
                          );
                        })}
                      </CTableRow>
                    );
                  }
                })}
              </CTableHead>
              <CTableBody>
                {tableData.map((r, i) => {
                  if (i > 0) {
                    return (
                      <CTableRow>
                        {r.map((r1, j) => {
                          if (j == 0) {
                            return <CTableDataCell>{r1.name}</CTableDataCell>;
                          } else {
                            return <CTableDataCell>{r1.name}</CTableDataCell>;
                          }
                        })}
                      </CTableRow>
                    );
                  }
                })}
              </CTableBody>
            </CTable>
          </CCol>
        </CRow>
      );
    } else {
      record.push(
        <CRow className="mt-2">
          <CCol md={12} style={{ textAlign: "center" }}>
            No record found
          </CCol>
        </CRow>
      );
    }
    return record;
  };

  // get teacher data from database
  const getClasses = async () => {
    try {
      await EDU_AXIOS.get(
        `${process.env.REACT_APP_API_PATH}/cm/get-sections/${school_id}`
      )
        .then((resp) => {
          let data = [];
          resp.data.data.map((d) => {
            return d.sections.map((s) => {
              let sd = {
                text: `${d.class}-${s}`,
                value: `${d.class}-${s}`,
              };
              data.push(sd);
            });
          });
          setClasses(data);
        })
        .catch((err) => {
          notify("error", err);
        });
      return true;
    } catch {
      return false;
    }
  };

  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>Time Table (Admin)</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Create Admin Time Table</p>
          {dataLoaded ? (
            <Formik
              initialValues={initialValues}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
              enableReinitialize={true}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CCol md={4}>
                    <CFormLabel htmlFor="class" className="required-field">
                      Type
                    </CFormLabel>
                    <EVFormikControl
                      control="selectwithkey"
                      name="class"
                      id="class"
                      placeholder="Select class..."
                      options={classes}
                    />
                  </CCol>
                  <CCol md={4}>
                    <CFormLabel htmlFor="start" className="required-field">
                      Start Date
                    </CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="date"
                      name="start_date"
                      id="start_date"
                    />
                  </CCol>
                  <CCol md={4}>
                    <CFormLabel htmlFor="end" className="required-field">
                      End Date
                    </CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="date"
                      name="end_date"
                      id="end_date"
                    />
                  </CCol>
                  <CCardFooter>
                    <CButton block color="light" onClick={openModalBox}>
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      block
                      color="dark"
                      type="submit"
                      disabled={
                        formik.isSubmitting || !formik.dirty || !formik.isValid
                      }
                      style={{ marginLeft: "15px" }}
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CCardFooter>
                  <CModal visible={visible} onDismiss={closeModalBox}>
                    <CModalHeader onDismiss={closeModalBox}>
                      <CModalTitle>
                        <h5>Cancel</h5>
                      </CModalTitle>
                    </CModalHeader>

                    <CModalBody>Do You Want To Cancel ?</CModalBody>
                    <CModalFooter>
                      <CButton color="light" onClick={closeModalBox}>
                        <FontAwesomeIcon icon={faTimesCircle} /> No
                      </CButton>
                      <Link to="/">
                        <CButton color="dark">
                          <FontAwesomeIcon icon={faCheckCircle} /> Yes
                        </CButton>
                      </Link>
                    </CModalFooter>
                  </CModal>
                </Form>
              )}
            </Formik>
          ) : (
            <Loader />
          )}
          <CRow>
            <CCol md={12}>{showTable && showTimeTable()}</CCol>
          </CRow>
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
};

export default create_admin;
